
.toggleDiv{
    position: absolute;
    cursor: pointer;
    top:50%;
    left:0px;
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
    background-color: rgba(255, 210, 77, 0.5);
  }
  .toggleDiv:hover{
    background-color: rgba(255, 210, 77, 0.7);
  }
  .modeImages{
    height: 3rem;
    width: 3rem;
    padding:0.5rem 0.5rem; 
  }